import React from 'react';
import { Link, useLocation,useHistory } from "react-router-dom";

export default function Navbar() {
  const history = useHistory ()
  let location = useLocation(); 
  const logout = () => {
    localStorage.removeItem('token')
    history.push('./login')
  }
  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            I-NoteBook
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link
                  className={`nav-link ${
                    location.pathname === "/" ? "nav-link active" : "nav-link"
                  }`}
                  aria-current="page"
                  to="/"
                >
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className={`nav-link ${
                    location.pathname === "/about"
                      ? "nav-link active"
                      : "nav-link"
                  }`}
                  to="/about"
                >
                  About
                </Link>
              </li>
            </ul>
            <form className="d-flex">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button className="btn btn-success mx-2" type="submit">
                Search
              </button>
            </form>
             
           {!localStorage.getItem('token') 
           ?
           <div>
            <Link className="btn btn-success mx-1" to="login" role="button">Login</Link>
            <Link className="btn btn-success mx-1" to="signup" role="button">SignUp</Link>
            </div>
          :
          <button className="btn btn-success my-2 mx-2" onClick={logout}>Logout</button>
          }
            
          </div>
        </div>
      </nav>
    </div>
  );
}